<template>
  <v-app>
    <div fluid>
      <!-- <Map ref="themap" /> -->
      <v-row align="center">
        <v-col cols="6">
          <v-select
            v-model="selected_route"
            :items="route"
            label="Select Route"
            persistent-hint
            return-objec
            single-line
            @change="give_to_station(selected_route)"
          ></v-select>
          <v-select
            v-model="selected_Entry_Station"
            :items="station_per_route"
            item-text="station_Name"
            label="Select Entry Route"
            persistent-hint
            return-objec
            single-line
          ></v-select>
          <v-select
            v-model="selected_Exit_Station"
            :items="station_per_route"
            item-text="station_Name"
            label="Select Exit Station"
            persistent-hint
            return-objec
            single-line
          ></v-select>
          <button @click="register_Transaction()">transact</button>
        </v-col>
        <v-col cols="6">
          <v-text-field
            label="Entrance"
            outlined
            dense
            :rules="inputRules"
            v-model="Entrance_input"
          />
          <v-text-field
            label="Exit"
            outlined
            dense
            :rules="inputRules"
            v-model="Exit_input"
          />
        </v-col>
      </v-row>
    </div>
  </v-app>
</template>

<script>
import route from "../service/Hello";
import Map from "./map/showLocation";
export default {
  components: {
    Map,
  },
  data() {
    return {
      selected_route: "",
      selected_Entry_Station: "",
      selected_Exit_Station: "",
      Entrance_input: "",
      Exit_input: "",
      route: [],
      stations: [],
      station_per_route: [],
      ticket_type: "Ticket",
      route_segments: [],
      route_length: [],
    };
  },
  async mounted() {
    // this.$refs.themap.ss([9.0180934, 38.8021491]);
    //  Map.ss([38.8021491, 9.0180934]);
    var data = (await route.get_machine_route()).data;
    if (data.success) {
      // console.log(data.message.length);
      // for (i = 0; i < 2; i++) {
      for (var i = 0; i < data.message.length; i++) {
        //console.log(1);
        this.route.push(data.message[i].route_Name);
        this.route_length.push(data.message[i].route_length);
        // this.select = this.route[0];
        this.stations.push(data.message[i].stations);
        this.route_segments.push(data.message[i].route_segments);
      }
      // console.log(this.route);
    }
  },
  methods: {
    // async getroute() {
    //   var data = (await route.get_route()).data;

    //   if (data.success) {
    //     // console.log(data.message.length);
    //     // for (i = 0; i < 2; i++) {
    //     for (var i = 0; i < data.message.length; i++) {
    //       //console.log(1);
    //       this.route.push(data.message[i].route_Name);
    //       // this.select = this.route[0];
    //       this.station.push(data.message[i].stations);
    //     }
    //     console.log(this.route);
    //     //  console.log(this.station);
    //   }
    //},
    async give_to_station(x) {
      for (var i = 0; i < this.route.length; i++) {
        // console.log(this.route[i])
        if (this.route[i] === x) {
          this.station_per_route = this.stations[i];
          console.log(this.stations[i]);
          break;
        }
      }
      console.log(x);
      console.log(this.station_per_route);
    },
    async register_Transaction() {
      //console.log(this.selected_Entry_Station);
      var transport_length = "";
      var entry = "";
      var exit = "";
      if (
        this.selected_Entry_Station == "" ||
        this.selected_Exit_Station == "" ||
        this.selected_Entry_Station == this.selected_Exit_Station
      ) {
        alert("please check your inputs");
        return;
      }
      for (var k = 0; k < this.stations.length; k++) {
        // console.log("kkkkkkkkkkk");
        for (var t = 0; t < this.stations[k].length; t++) {
          //  console.log(this.stations[k][t]);
          if (this.selected_Entry_Station == this.stations[k][t].station_Name)
            entry = this.stations[k][t]._id;
          if (this.selected_Exit_Station == this.stations[k][t].station_Name)
            exit = this.stations[k][t]._id;
        }
        if (entry != "" && exit != "") {
          //  console.log(this.route_length[k]);
          transport_length = this.route_length[k];
          //  console.log(transport_length);
          break;
        }
      }
      for (var i = 0; i < this.route.length; i++) {
        for (var j = 0; j < this.route_segments[i].length; j++) {
          if (this.route_segments[i][j].sub_route[0] == entry) {
            if (this.route_segments[i][j].sub_route[1] == exit) {
              transport_length = this.route_segments[i][j].sub_route_length;
              break;
            }
          } else if (this.route_segments[i][j].sub_route[0] == exit) {
            if (this.route_segments[i][j].sub_route[1] == entry) {
              transport_length = this.route_segments[i][j].sub_route_length;
              break;
            }
          }

          //   .includes([entry, exit])) {
          //     transport_length = this.route_segments[i][j].sub_route_length;
          //     break;
          //   }
        }
      }
      var transaction = {
        route: this.selected_route,
        entry_Station: this.selected_Entry_Station,
        exit_Station: this.selected_Exit_Station,
        ticket: this.Exit_input,
        ticket_Type: this.ticket_type,
        transport_length: transport_length,
      };
      console.log(transaction);
      var data = await route.register_Transaction(transaction);
      console.log(data);
      if (data.status != 401) {
        if (data.data.success) {
          console.log("succesfully registered");
        } else {
          console.log("problem with the transaction");
        }
      } else {
        console.log("anautherized");
      }
    },
  },
};
</script>

