<template>
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-center">Entry Station</th>
          <th class="text-center">Exit Station</th>
          <th class="text-center">Ticket Amount</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in tickets" :key="item._id" @click="generate_Qr(item)">
          <td>{{ item.entry_Station.station_Name }}</td>
          <td>{{ item.exit_Station.station_Name }}</td>
          <td>{{ item.ticket_Amount }}</td>
        </tr>
      </tbody>
      <!-- <qrcode-vue :value="value" :size="size" level="H" /> -->
      <!-- <qrcode-vue
        style="cursor: pointer;"
        :value="value"
        size="300"
        level="M"
        :background="backgroundcolor_qrcode"
        :foreground="color_qrcode"
      ></qrcode-vue>-->
      <Vue-qr-code v-model="value" />
    </template>
  </v-simple-table>
</template>

<script>
import hello from "../../service/Hello";
// import Vue from "vue";
// import QrcodeVue from "qrcode.vue";
// Vue.component("qrcode-vue", QrcodeVue);

import VueQrCode from "vue-weblineindia-qrcode";
export default {
  components: {
    VueQrCode,
  },
  data() {
    return {
      tickets: [],
      value: "hello gs",
      size: 300,
    };
  },
  async mounted() {
    await this.get_Ticket();
  },
  methods: {
    async get_Ticket() {
      var result = (await hello.get_ticket(this.$store.state.token2)).data;
      if (result.success) {
        if (!Array.isArray(result.message)) {
          result.message = [result.message];
        }
        for (var i = 0; i < result.message.length; i++) {
          this.tickets.push(result.message[i]);
        }
        console.log(this.tickets);
      }
    },
    async generate_Qr(ticket) {
      // console.log("hellllo");
      var result = (
        await hello.generate_Ticket(
          {
            entry_Station: ticket.entry_Station.station_Name,
            exit_Station: ticket.exit_Station.station_Name,
            route: ticket.route.route_Name,
            ticket: ticket.ticket,
            date: ticket.issued_Date,
          },
          this.$store.state.token2
        )
      ).data;
      // document.getElementById();
      this.value = result.ticket;
      console.log(result);
    },
  },
};
</script>

