<template>
  <div>
    <v-row justify="space-around">
      <v-col md="6" l="6" sm="6">
        <h1>report</h1>
      </v-col>
    </v-row>
    <v-card elevation="0">
      <v-tabs fixed-tabs v-model="tab" flat>
        <v-tabs-slider color="primary"> </v-tabs-slider>

        <v-tab
          style="padding-inline: 60px"
          justify="space-around"
          v-for="(item, index) in itemese"
          :class="{ active: ct === index }"
          @click="ct = index"
          :key="item"
        >
          {{ item }}
        </v-tab>

        <v-tabs-items v-model="tab">
          <div v-if="ct === 0">
            <v-card class="ma-0" elevation="0"
              ><h2>customer report</h2>
              <!-- <AreaChart /> -->
              <v-container>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="12" md="6" l="6">
                    <v-card elevation="0"> <PieChart /></v-card>
                  </v-col>
                  <v-col cols="12" xs="12" sm="4" md="4" l="4">
                    <v-card elevation="0" min-width="500" class="ma-4"></v-card>
                    <v-card elevation="0"> <AnotherAreaChart /></v-card>
                  </v-col>
                </v-row>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="12" md="6" l="6">
                    <v-card elevation="0"> <AnotherBarChart /></v-card>
                  </v-col>
                  <v-col cols="12" xs="12" sm="4" md="4" l="4">
                    <v-card elevation="0" min-width="500" class="ma-4"></v-card>
                    <v-card> <RadarChart /></v-card>
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </div>

          <div v-if="ct === 1">
            <v-card flat>
              transaction report
              <v-container>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <BarChart />
                  </v-col>
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <LineChart />
                  </v-col>
                </v-row>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <DoughnutChart />
                  </v-col>
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <AnotherPieChart />
                  </v-col>
                </v-row>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <AreaChart />
                  </v-col>
                  <v-col cols="12" xs="12" sm="6" md="6" l="6">
                    <AnotherAreaChart2 />
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </div>
          <div v-if="ct === 2">
            <v-card class="ma-4"
              ><h2>organization report{{ ct }}</h2>
              <v-container>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="12" md="6" l="6">
                    <PolarAreaChart />
                  </v-col>
                  <v-col cols="12" xs="12" sm="4" md="4" l="4">
                    <v-card elevation="0" min-width="500" class="ma-4"></v-card>
                    <v-card> <AnotherPieChart /></v-card>
                  </v-col>
                </v-row>
                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="12" md="12" l="12">
                    <AnotherPolarAreaChart />
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </div>

          <div v-if="ct === 3">
            <v-card flat>
              <h2>machine</h2>
              <v-container>
                <v-row justify="space-around">
