                <v-row justify="space-around">
                  <v-col cols="12" xs="12" sm="12" md="12" l="12">
                    <PolarAreaChart />
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </div>
        </v-tabs-items>
      </v-tabs>
    </v-card>
    <div>
      <button @click="printSection">Print Shit</button>
    </div>
  </div>
</template>

<script>
import AreaChart from "./chart/Transaction Made Per Entry Station AreaChart";
import PieChart from "./chart/Number of Customer Per Access Type PieChart";
import BarChart from "./chart/Transaction per route BarChart";
import RadarChart from "./chart/Number of Customer By Gender RadarChart";
import LineChart from "./chart/Transaction Made In Transaction Amount LineChart";
import DoughnutChart from "./chart/Transaction Per payment type DoughnutChart";
import PolarAreaChart from "./chart/Number Of Buses Per Service Provider PolarAreaChart";
import BubbleChart from "./chart/BubbleChart";
import ScatterdChart from "./chart/ScatteredChart";
import AnotherPieChart from "./chart/Transaction Per Organization PieChart";
import AnotherAreaChart from "./chart/Customer By Age Range AreaChart";
import AnotherAreaChart2 from "./chart/Transaction Made Per Exit Station AreaChart";
import AnotherBarChart from "./chart/Customer registered per month BarChart";
import AnotherBarChart2 from "./chart/Total Numebr Of Transaction BarChart";
import AnotherPieChart2 from "./chart/Machine Per Machine Type PieChart";
import AnotherDoughnutChart from "./chart/Number Of Customer Per Customer Type DoughnutChart";
import AnotherLineChart from "./chart/LineChart";
import AnotherPolarAreaChart from "./chart/Number Of Employee Per Organization PolarAreaChart";
//import { Printd } from "printd";
//import html2pdf from "html2pdf.js";
export default {
  name: "App",
  components: {
    AreaChart,
    PieChart,
    BarChart,
    RadarChart,
    LineChart,
    DoughnutChart,
    PolarAreaChart,
    BubbleChart,
    ScatterdChart,
    AnotherPieChart,
    AnotherAreaChart,
    AnotherAreaChart2,
    AnotherBarChart,
    AnotherBarChart2,
    AnotherPieChart2,
    AnotherDoughnutChart,
    AnotherLineChart,
    AnotherPolarAreaChart,
  },
  data() {
    return {
      chartData: {
        Books: 24,
        Magazine: 30,
        Newspapers: 10,
        tab: null,
      },
      itemese: ["customer", "transaction", "organization"],
      ct: 0,
      tab: null,
    };
  },
  mounted() {
    var d = new Printd();
    var cssText = `
    .box {
      font-family: sans-serif;
      width: 500px;
      border: solid 1px #ccc;
      text-align: center;
      padding: 1em;
      margin: 2em auto;
    }
    button {
      background-color: #f0f0f0;
      border: solid 1px #ccc;
      padding: 5px 10px;
      font-size: 12px;
    }
    pre {
      color: #c7254e;
    }
  `;
    this.d = new Printd();
    //  this.fetchPrintStuff();
  },
  methods: {
    // ...mapActions("myModule", ["fetchPrintStuff"]),
    printShit() {
      this.d.print(this.$el, this.cssText);
    },
    exportToPDF() {
      html2pdf();
      html2pdf(this.$refs.document, {
        margin: 1,
        filename: "document.pdf",
        image: { type: "jpeg", quality: 0.98 },
        html2canvas: { dpi: 192, letterRendering: true },
        jsPDF: { unit: "in", orientation: "landscape" },
      });
      //  console.log('llllllllll')
    },
    printSection() {
      this.$htmlToPaper("printSection");
    },
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.v-tab {
  padding-right: 40px;
}
</style>
